.postsBlock {
    margin: 0 auto;
    margin-top: 60px;
    margin-left: 20px;
    position: relative;
}

.postsBlock h3 {
    text-shadow: 0 0 5px #ffffff;
    text-transform: uppercase;
}

.posts {
    padding: 0;
    margin: 0;
    margin-top: 30px;

    list-style: none;
}

.postsBlock2 {
    width: 100px;
}

.postsBlock1 textarea {
    width: 50%;
    height: 80px;
}

.postsBlock2 button {
    width: 100%;
    height: 40px;
}

@media only screen and (min-width: 320px) and (max-width: 480px) {
    .postsBlock1 textarea {
        margin: 0;
        padding: 0;
        width: 100%;
    }

    .postsBlock {
        margin: 0;
        padding: 0;
    }
}
